@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: medium;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 768px) and (max-width: 1280px) {
  .container--narrow {
    padding-left: 70px;
    padding-right: 70px;
  }
}

.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
}

.page-header__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 11px;
}
.page-header__wrapper::after {
  content: "";
  position: absolute;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 1px;
  background-color: #e6e6e6;
  bottom: 0;
}
@media (min-width: 768px) {
  .page-header__wrapper::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
  }
}
@media (min-width: 1280px) {
  .page-header__wrapper {
    padding-top: 53px;
  }
}

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .page-header__top {
    padding-bottom: 0;
  }
}

.page-header__logo {
  display: block;
  height: 38px;
}
.page-header__logo[href]:hover {
  opacity: 0.8;
}
.page-header__logo[href]:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__logo {
    height: 52px;
  }
}
@media (min-width: 1280px) {
  .page-header__logo {
    height: 59px;
  }
}

.page-header__nav {
  width: 100%;
}
@media (min-width: 768px) {
  .page-header__nav {
    max-width: 450px;
  }
}
@media (min-width: 1280px) {
  .page-header__nav {
    max-width: 590px;
  }
}

.page-header__nav-toggle {
  cursor: pointer;
}

.page-main {
  overflow: hidden;
}

.no-js .main-nav {
  display: block;
  position: static;
  min-height: 0;
}

@media (max-width: 767px) {
  .main-nav--closed {
    display: none;
  }
}

.burger-button {
  position: relative;
  width: 24px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .burger-button {
    display: none;
  }
}

.no-js .burger-button {
  display: none;
}

.burger-button--closed .burger-button__decoration {
  position: absolute;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.burger-button--closed .burger-button__decoration::before, .burger-button--closed .burger-button__decoration::after {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.burger-button--closed .burger-button__decoration::before {
  transform: translateY(-8px);
}
.burger-button--closed .burger-button__decoration::after {
  transform: translateY(8px);
}

.burger-button--opened .burger-button__decoration::before, .burger-button--opened .burger-button__decoration::after {
  content: "";
  position: absolute;
  display: block;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
}
.burger-button--opened .burger-button__decoration::before {
  transform: rotate(45deg);
}
.burger-button--opened .burger-button__decoration::after {
  transform: rotate(-45deg);
}

.site-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .site-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

.site-nav__item {
  position: relative;
  padding-left: 22px;
  padding-right: 22px;
  text-align: center;
}
.site-nav__item::before {
  content: "";
  position: absolute;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 1px;
  background-color: #e6e6e6;
  top: 0;
}
@media (min-width: 768px) {
  .site-nav__item::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-nav__item {
    margin-left: 35px;
    padding-left: 0;
    padding-right: 0;
  }
  .site-nav__item:first-child {
    margin-left: 0;
  }
}

.site-nav__item a {
  position: relative;
  display: block;
  padding-top: 23px;
  padding-bottom: 23px;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .site-nav__item a {
    padding-top: 18px;
    padding-bottom: 10px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1280px) {
  .site-nav__item a {
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 20px;
    line-height: 30px;
  }
}

.site-nav__item a[href]:hover {
  opacity: 0.6;
}
.site-nav__item a[href]:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .site-nav__item--current a::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68b738;
  }
}

@media (min-width: 1280px) {
  .site-nav__item--index a {
    color: #ffffff;
  }
}

@media (min-width: 1280px) {
  .site-nav__item--index.site-nav__item--current a::after {
